import React, {useEffect, useState} from 'react'

// Mock styles since we don't have the actual CSS modules
const s2 = {
    hwTitle: 'text-2xl font-bold mb-4 text-blue-600',
    hw: 'p-4 border border-gray-200 rounded-lg bg-gray-50'
}

const s = {
    loading: 'text-sm text-gray-500 my-2',
    tech: 'inline-block bg-blue-100 text-blue-800 px-3 py-1 rounded-full text-sm mr-2 mb-2'
}

// Типы для SuperDebouncedInput
interface SuperDebouncedInputProps {
    id: string
    value: string
    onChangeText: (value: string) => void
    onDebouncedChange: (value: string) => void
}

// SuperDebouncedInput component with completed onChangeTextCallback
const SuperDebouncedInput: React.FC<SuperDebouncedInputProps> = ({
                                                                     id,
                                                                     value,
                                                                     onChangeText,
                                                                     onDebouncedChange
                                                                 }) => {
    const [localValue, setLocalValue] = useState<string>(value)

    // Debounce effect
    useEffect(() => {
        const timeoutId = setTimeout(() => {
            onDebouncedChange(localValue)
        }, 1500) // 1.5 second debounce

        return () => clearTimeout(timeoutId)
    }, [localValue, onDebouncedChange])

    // Update local value when prop value changes
    useEffect(() => {
        setLocalValue(value)
    }, [value])

    const onChangeTextCallback = (e: React.ChangeEvent<HTMLInputElement>) => {
        const newValue = e.currentTarget.value
        setLocalValue(newValue)
        onChangeText(newValue) // Call parent's onChangeText immediately
    }

    return (
        <input
            id={id}
            type="text"
            value={localValue}
            onChange={onChangeTextCallback}
            placeholder="Поиск технологий..."
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
        />
    )
}

// Типы для API
interface TechsResponse {
    techs: string[]
}

interface ApiResponse {
    data: TechsResponse
}

// Mock API function to simulate the real getTechs function
const getTechs = (find: string): Promise<ApiResponse> => {
    // Simulate API delay
    return new Promise((resolve) => {
        setTimeout(() => {
            const allTechs = [
                'React', 'JavaScript', 'TypeScript', 'Node.js', 'Express',
                'MongoDB', 'PostgreSQL', 'Docker', 'Redux', 'Next.js',
                'Vue.js', 'Angular', 'Python', 'Java', 'Spring Boot',
                'AWS', 'Azure', 'Kubernetes', 'Jest', 'Cypress'
            ]

            const filteredTechs = find
                ? allTechs.filter(tech =>
                    tech.toLowerCase().includes(find.toLowerCase())
                )
                : allTechs.slice(0, 5) // Show first 5 if no search

            resolve({data: {techs: filteredTechs}})
        }, 500)
    })
}

const HW14: React.FC = () => {
    const [find, setFind] = useState<string>('')
    const [isLoading, setLoading] = useState<boolean>(false)
    const [techs, setTechs] = useState<string[]>([])

    // Mock useSearchParams functionality since it's not available in this environment
    const [searchParams, setSearchParamsState] = useState<URLSearchParams>(new URLSearchParams())

    const setSearchParams = (params: Record<string, string>) => {
        const newParams = new URLSearchParams(params)
        setSearchParamsState(newParams)
        // In real app, this would update the URL
        console.log('Search params updated:', Object.fromEntries(newParams))
    }

    const sendQuery = (value: string) => {
        setLoading(true)
        getTechs(value)
            .then((res: ApiResponse) => {
                // делает студент

                // сохранить пришедшие данные
                if (res && res.data && res.data.techs) {
                    setTechs(res.data.techs)
                } else {
                    setTechs([])
                }

                setLoading(false)
            })
            .catch(() => {
                setTechs([])
                setLoading(false)
            })
    }

    const onChangeText = (value: string) => {
        setFind(value)
        // делает студент

        // добавить/заменить значение в query урла
        setSearchParams({find: value})
    }

    useEffect(() => {
        const params = Object.fromEntries(searchParams)
        const findParam = params.find || ''
        sendQuery(findParam)
        setFind(findParam)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const mappedTechs = techs.map((t: string) => (
        <div key={t} id={'hw14-tech-' + t} className={s.tech}>
            {t}
        </div>
    ))

    return (
        <div id={'hw14'} className="max-w-2xl mx-auto p-6">
            <div className={s2.hwTitle}>Homework #14</div>

            <div className={s2.hw}>
                <SuperDebouncedInput
                    id={'hw14-super-debounced-input'}
                    value={find}
                    onChangeText={onChangeText}
                    onDebouncedChange={sendQuery}
                />

                <div id={'hw14-loading'} className={s.loading}>
                    {isLoading ? '...ищем' : ' '}
                </div>

                <div className="mt-4">
                    {mappedTechs}
                </div>
            </div>
        </div>
    )
}

export default HW14